/* AskQuestion.css */

/* Overall background for the modal */
.ask-question {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Ensures it's on top of other content */
}

/* Modal content styling */
.ask-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  width: 50%; /* Adjust width as necessary */
  max-width: 600px; /* Adjust max-width as necessary */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* subtle shadow for depth */
  position: relative; /* For absolute positioning of the close button */
  display: flex;
  flex-direction: column; /* Stack children elements vertically */
}

/* Close button at the top-right corner of the modal */
.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: none;
  font-size: 18px;
  cursor: pointer;
}

/* Spacing and layout for form elements */
.ask-content h2,
.ask-content form label,
.ask-content form input,
.ask-content form textarea,
.ask-content form button {
  display: block; /* Makes elements block-level for full width */
  width: calc(100% - 20px); /* Full width minus padding */
  margin-bottom: 10px; /* Spacing between elements */
}

/* Style for the form title */
.ask-content h2 {
  font-size: 24px;
  margin-bottom: 20px; /* More space below the title */
}

/* Style for form inputs and textarea */
.ask-content form input[type="text"] {
  /* Style for the smaller title input */
  padding: 10px;
  margin-bottom: 20px; /* More space after the title input */
}

.ask-content form textarea {
  /* Style for the larger question textarea */
  padding: 15px;
  height: 150px; /* Larger height for text area */
  margin-bottom: 20px; /* More space after the textarea */
}

/* Style for the tags input, assuming you have one */
.ask-content form input[name="tags"] {
  /* Same styles as other inputs */
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 10px; /* If you need space above the tags input */
}

/* Style for the submit button */
.ask-content form button {
  padding: 10px 20px; /* Button padding */
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: auto; /* Allow button to size to its content */
  margin-top: 20px; /* Space from the last input to button */
  align-self: flex-end; /* Align to the right within the flex container */
}

/* Hover effect for the submit button */
.ask-content form button:hover {
  background-color: #0056b3;
}

/* Additional styling to match the provided layout */
.ask-content form label {
  font-weight: bold; /* Make labels bold */
}
