.questions-list {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
  }
  
  .questions-list header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .questions-list .logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  .questions-list header input[type="search"] {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 200px; /* Adjust width as needed */
  }
  
  .questions-list header button {
    padding: 10px 15px;
    background-color: #888;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-left: 10px; /* Spacing between buttons */
  }
  
  .questions-list header button:hover {
    background-color: #555;
  }
  
  .questions-list .questions {
    border-top: 2px solid #eee;
  }
  
  .questions-list .question {
    border-bottom: 1px solid #eee;
    padding: 10px 0;
  }
  
  .questions-list .question:last-child {
    border-bottom: none;
  }
  
  .questions-list .question .votes {
    font-size: 24px;
    font-weight: bold;
    margin-right: 10px;
    display: inline-block; /* Or flexbox if you prefer */
  }
  
  .questions-list .question .content {
    display: inline-block;
    vertical-align: top;
  }
  
  .questions-list .question .content .title {
    font-size: 18px;
    color: #333;
  }
  
  .questions-list .question .content .summary {
    font-size: 14px;
    color: #666;
    margin-top: 5px;
  }
  
  .questions-list .question .content .tags {
    margin-top: 10px;
  }
  
  .questions-list .question .content .tags .tag {
    display: inline-block;
    padding: 5px 10px;
    margin-right: 5px;
    background: #eee;
    border-radius: 4px;
    font-size: 12px;
  }
  